<template>
	<div>
		<div class="main-body" ref="wrapper" :style="{height: (wrapperHeight-50) + 'px' }">
			<mt-loadmore :bottom-method="loadBottom" :bottom-add-loaded='allLoaded' ref="loadmore" :autoFill="isAutoFill">
				<ul>
					<li v-for="(item,index) in meiTuList" :key="index">
						<img :src="item.url" alt="">
					</li>
				</ul> 
			</mt-loadmore>
		</div>
	</div>
</template>
<script>
	export default{
		data(){
			return {
				page:10,
				meiTuList:[],
				allLoaded:false,
				isAutoFill:false,
				wrapperHeight:0,
			}
		},
		created(){
			this.axiosMeitu();//  初次加载数据
		},
		  mounted() {
		    
		    this.wrapperHeight =
		      document.documentElement.clientHeight -
		      this.$refs.wrapper.getBoundingClientRect().top;
		  },
		methods:{
			loadBottom(){
				console.log(11);
				this.loadMore();
			},
			axiosMeitu(){ //  初次加载数据
				this.$axios.get(`https://www.apiopen.top/meituApi?page=${this.page}`)
				.then(res=>{
					 this.allLoaded = false; // 可以进行上拉
					this.meiTuList = res.data.data;
					this.page++;
				})
				.catch(err=>{
					console.log(err)
				})
			},
					  // 加载更多
		    loadMore() {
		        this.$axios.get(`https://www.apiopen.top/meituApi?page=${this.page}`)
	         	.then(res => {
	         		 // concat数组的追加
	          		this.meiTuList = this.meiTuList.concat(res.data.data);
	          		if (res.data.data.length <= 0) {
	            		this.allLoaded = true; // 若数据已全部获取完毕
	          		}
			        this.page++;
			        this.$refs.loadmore.onBottomLoaded();
	        	})
		        .catch(error => {
		          console.log(error);
		          alert("网络错误，不能访问");
		        });
		    }

		}
	}
</script>
<style lang="less" scoped>
	.main-body {
	  /* 加上这个才会有当数据充满整个屏幕，可以进行上拉加载更多的操作 */
	  overflow: scroll;
	}
	ul{
		li{
			margin-bottom: 10px;
			img{
				width: 100%;
			}	
		}
	}
	
</style>


